.reconnectContainer{
    border: solid 3px var(--main-color);
    border-radius: 10px;
    position: absolute;
    width: 300px;
    height: 50px;
    top: calc(40% - 25px);
    left: calc(50% - 170px);
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}